/* div에 스타일 적용 */
.selectDiv {
    border: 1px solid #ccc;
    padding: 10px;
    background-color: #f2f2f2;
    width: 49%;
    max-width: 100%;
}

/* 테이블 컨테이너 */
.playerTable {
    border: 1px solid #ccc;
    border-collapse: collapse;
    width: 50%;
    max-width: 100%;
}

/* 테이블 헤더와 셀 스타일 */
.playerTable th, .playerTable td {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: center;
    font-weight: bold;
}

/* 불필요한 셀 내용 여백 제거 */
.playerTable td:first-child, .playerTable th:first-child {
    padding-left: 0;
}

/* 배경색 및 텍스트 색상 설정 */
.playerTable th {
    background-color: #f2f2f2;
    color: #333;
}

.player-list-container {
    float: left;
    width: 100%;
    padding: 20px;
}

.modal {
    display: block;
    position: fixed;
    top: 50%; /* 모달을 수직 중앙 정렬합니다. */
    left: 50%; /* 모달을 수평 중앙 정렬합니다. */
    transform: translate(-50%, -50%); /* 모달을 양방향으로 중앙 정렬합니다. */
    background-color: white;
    border: 1px solid #ccc;
    padding: 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    max-width: 80%; /* 모달의 최대 너비를 설정합니다. */
    width: auto; /* 모달을 콘텐츠에 따라 너비를 조정할 수 있게 합니다. */
}

.modal-content {
    max-width: 400px; /* "기록이 없습니다" 텍스트 영역의 최대 너비를 설정합니다. */
    margin: 0 auto; /* 수평 중앙 정렬을 수행합니다. */
}
